<template>
    <upgrade-step :header="header" 
                :stepText="stepText" 
                :helpMessage="helpMessage"
                :uploadSuccess="uploadSuccess" 
                :uploadFailure="uploadFailure"
                @goToUpgrade="goToUpgrade"
                >

        <!-- 第一部分：填写认证信息 -->
        <form slot="firstStep" class="modify-form">
            <div class="form-control">
                <label class="label-title">企业简称：</label>
                <input class="input-control" type="text" name="username" placeholder="输入企业的简称" />
                <p class="tips">认证后，您的账号昵称将显示为企业简称，并配有企业认证标识</p>
            </div>
            <div class="form-control upload-ID">
                <label class="label-title">企业营业执照：</label>
                <div class="upload-file">
                    <div class="up">
                        <o-button :text="'点击上传'" :classStyle="'main-btn'" class="upload-btn" @btnClick=""></o-button>
                        <input type="file" class="input-control" />
                    </div>
                </div>
            </div>
        </form>

        <!-- 第二部分：上传认证资料 -->
        <div slot="secondStep" class="pay-tab">
            <pay-tabs>
                <p class="text" slot="message">支付企业认证费：<strong>300</strong>元/年 </p>
            </pay-tabs>
        </div>

        <!-- 点击确认按钮 -->
        <o-button :text="'确定'" :classStyle="'main-btn'" class="btn" @btnClick="upgradeSuccess" slot="makeSureBtn"></o-button>
    </upgrade-step>
</template>

<script>

    import OButton from '../../component/button.vue';
    import UpgradeStep from '../upgradeStep.vue';
    import PayTabs from '../../component/payTabs.vue';

    export default {
        data() {
            return {
                header: "升级企业号", // 升级的标题
                stepText: { // 步骤条的信息
                    "first": "填写企业信息",
                    "second": "支付企业认证费"
                },
                helpMessage: "您的企业号已开通成功！到期时间为：2019-6-20", // 认证时提示信息
                uploadSuccess: true, // 上传成功
                uploadFailure: false, // 上传失败
                // WeixinFlag: true, // 微信支付
                // AlipayFlag: false, // 支付宝支付
                updateFlag: false, // 升级为企业号标识
                // payTabs: [ // 支付tabs
                //     { "text": "微信支付" },
                //     { "text": "支付宝支付" }
                // ],
                // payContents: [ // 支付方式
                //     { "src": "/static/images_icon/weixin.png", "text": "打开微信扫一扫" },
                //     { "src": "/static/images_icon/weixin.png", "text": "打开支付宝扫一扫" }
                // ],
                // curId: 0, // 切换支付方式的id
            }
        },
        methods: {

            // 关闭弹窗
            goToUpgrade() {
                this.$emit('goToUpgrade');
            },

            // 改变支付方式
            // changePayMethods (index) {
            //     this.curId = index;

            // },

            // 支付成功后点击确定按钮
            upgradeSuccess () {
                this.$emit('goToUpgrade', !this.updateFlag);
            }
        },
        components: {
            OButton,
            UpgradeStep,
            PayTabs
        }
    }
</script>
<style lang="scss" rel="stylesheet/scss" scoped media="screen">

            // 第一部分：填写企业认证信息
            .modify-form {
                height: 275px;
                .form-control {
                    width: 94%;
                    margin: 20px auto 0;
                    .label-title {
                        width: 108px;
                        text-align: right;
                        float: left;
                        line-height: 32px;
                        font: {
                            size: small;
                            weight: bold;
                        }
                    }
                    .input-control {
                        display: block;
                        width: 164px;
                        height: 32px;
                        border-radius: 4px;
                        float: left;
                        background-color: #F4F4F4;
                        padding: 0 6px;
                        font-size: 13px;
                        margin-bottom: 10px;
                    }
                    .w245 {
                        width: 245px;
                    }
                    .tips {
                        width: 280px;
                        margin-left: 107px;
                        clear: both;
                        font-size: x-small;
                        color: #999;
                    }
                }
                .upload-ID {
                    .up {
                        position: relative;
                        input[type=file] {
                            width: 93px;
                            height: 30px;
                            position: absolute;
                            left: 109px;
                            top: 0px;
                            z-index: 2;
                            opacity: 0;
                            -ms-filter: 'alpha(opacity=0)';
                            cursor: pointer;
                        }
                        .upload-btn {
                            width: 95px;
                            height: 30px;
                        }
                    }
                }
            }

            // 第二部分：选择地点/作品标签/作品价格
            // .pay-tab {
            //     .header {
            //         height: auto;
            //         text-align: center;
            //         .pay-btn {
            //             width: 120px;
            //             height: 38px;
            //             display: inline-block;
            //             &:first-child {
            //                 border-radius: 3px 0 0 3px;
            //             }
            //             &:last-child {
            //                 border-radius: 0 3px 3px 0;
            //             }
            //         }
            //         .active {
            //             color: #e5f5ff;
            //             background: #48ABFF;
            //             border-color: #48abff;
            //         }

            //     }
            //     .pay-content {
            //         width: 200px;
            //         margin: 30px auto;
            //         text-align: center;
            //         .pay-img {
            //             display: block;
            //             width: 110px;
            //             margin: 0 auto;
            //             height: 110px;
            //         }
            //         p.text {
            //             font-size: small;
            //             line-height: 1;
            //             padding-top: 10px;
            //         }
            //     }
            // }
            .btn {
                width: 78px;
                height: 34px;
                margin: 50px auto;
            }
        
</style>


