<template>
    <popup>
        <div slot="content" class="release-popup">

            <!-- 弹框标题 -->
            <h2 class="header">
                {{ header }}
            </h2>

            <!-- 艺术家认证步骤 -->
            <div class="upgrade-step">
                <div class="active" ref="first">
                    <span class="num-step">1</span>
                    <span>{{ stepText.first }}</span>
                    <i class="spot">........</i>
                </div>
                <div ref="second">
                    <span class="num-step">2</span>
                    <span>{{ stepText.second }}</span>
                    <i class="spot">........</i>
                </div>

                <!-- 上传成功 -->
                <div ref="over">
                    <span class="num-step">3</span>
                    <span ref="fail">完成</span>
                </div>
            </div>

            <!-- 弹框内容 -->
            <div class="container-wrapper" ref="selectBox">

                <!-- 第一部分：填写认证信息 -->
                <div class="slide-box upload-box">
                    <slot name="firstStep"></slot>
                    <div class="alert-btn-box">
                        <o-button :text="'下一步'" :classStyle="'main-btn'" class="next-btn" @btnClick="nextStep"></o-button>
                    </div>
                </div>

                <!-- 第二部分：上传认证资料 -->
                <div class="slide-box select-box">
                    <slot name="secondStep"></slot>
                    <div class="buttons">
                        <o-button :text="'上一步'" :classStyle="'main-btn'" class="btn" @btnClick="returnStep"></o-button>
                        <o-button :text="'提交'" :classStyle="'main-btn'" class="btn" @btnClick="relaseStep"></o-button>
                    </div>
                </div>

                <!-- 第三部分：完成 -->
                <div class="slide-box">

                    <!-- 升级为企业账号成功 -->
                    <div class="upload-result" v-if="uploadSuccess">
                        <div class="uploading">
                            <span class="upload-img success-img"></span>
                            <p class="upload-text">支付成功</p>
                        </div>
                        <p class="help-message">
                            {{ helpMessage }}
                        </p>
                        <slot name="makeSureBtn"></slot>
                    </div>

                    <!-- 提交失败 -->
                    <div class="upload-result" v-if="uploadFailure">
                        <div class="uploading">
                            <span class="upload-img error-img"></span>
                            <p class="upload-text">上传失败</p>
                        </div>
                        <p class="help-message">
                            如遇到无法解决的问题，请<a href="javascript:;">联系客服</a>
                        </p>
                        <o-button :text="'重新支付'" :classStyle="'main-btn'" class="btn" @btnClick="reUpgrade"></o-button>
                    </div>
                </div>
            </div>
        </div>
        <input type="button" name="" class="close-btn" slot="close" @click="closePopup" />
    </popup>
</template>

<script>
    
    import OButton from '../component/button.vue';
    import Popup from '../component/popup.vue';
    
    export default {
        data() {
            return {
                elem: this.$refs.selectBox, // 上传作品 的步骤
            }
        },
        methods: {

            // 关闭弹窗
            closePopup() {
                console.log('hihi');
                this.$emit('goToUpgrade');
            },

            // 下一步
            nextStep () {
                this.changeStep(this.$refs.selectBox, "back", "next", 397, this.$refs.first, this.$refs.second);
            },

            // 返回上一步
            returnStep () {
                this.changeStep(this.$refs.selectBox, "next", "back", 397, this.$refs.second, this.$refs.first);
            },

            // 提交认证
            relaseStep () {
                this.changeStep(this.$refs.selectBox, "next", "last", 340, this.$refs.second, this.$refs.over);

                if (this.uploadFailure) {
                    this.$refs.over.classList.add("failure-message");
                    this.$refs.fail.innerHTML = "失败";

                }
            },

            // 支付失败 重新支付
            reUpgrade () {
                this.changeStep(this.$refs.selectBox, "last", "back", 390, this.$refs.over, this.$refs.first);
                this.$refs.over.classList.remove("failure-message");
                this.$refs.fail.innerHTML = "完成";
            },

            // 上传作品 的步骤
            changeStep(elem, removeClass, addClass, height, reElem, addElem) {
                elem.classList.remove(removeClass);
                elem.classList.add(addClass);
                elem.style.height = height + 'px';
                reElem.classList.remove('active');
                addElem.classList.add('active');
            }
        },
        components: {
            OButton,
            Popup
        },
        props: ["header", "stepText", "helpMessage", "uploadSuccess","uploadFailure"]
    }
</script>
<style lang="scss" rel="stylesheet/scss" scoped media="screen">
    
    /*发布弹框*/
    .release-popup {
        overflow: hidden;
        width: 500px;
        .header {
            height: 43px;
            padding-top: 18px;
            font-size: xx-large;
            text-align: center;
        }

        // 企业升级步骤
        .upgrade-step {
            text-align: center;
            padding: 50px 0;
            font: {
                size: x-small;
                weight: bold;
            }
            color: #464545;

            div, span {
                display: inline-block;
            }
            .active {
                color: #48abff;
                .num-step {
                    background: #48abff;
                    color: #fff;
                }
            }
            .failure-message {
                color: #fe6448;
                .num-step {
                    background: #fe6448;
                    color: #fff;
                }
            }
            span.num-step {
                width: 20px;
                height: 20px;
                text-align: center;
                line-height: 20px;
                border-radius: 50%;
                background: #f1f1f1;
            }
            i.spot {
                display: inline-block;
                position: relative;
                top: -4px;
            }

        }
        .next {
            margin-left: -100%;
        }
        .last {
            margin-left: -200%;
        }
        .back {
            margin-left: 0;
        }

        .container-wrapper {
            width: 300%;
            height: 397px;
            max-height: 85vh;
            overflow: hidden;
            transition: all 0.3s cubic-bezier(.07, .63, .39, .98);
            .slide-box {
                width: 33.33%;
                float: left;
                padding: 0 30px;
                overflow-y: auto;
                max-height: 100%;
            }
            .next-btn {
                display: flex;
                justify-content: center;
                overflow: hidden;
                margin: 30px auto;
                box-shadow: 0 3px 5px rgba(0, 0, 0, 0.10);
                width: 78px;
                height: 34px;
            }

            // 第二部分：选择地点/作品标签/作品价格
            .select-box {
                .buttons {
                    width: 182px;
                    margin: 0 auto;
                    .btn {
                        margin: 20px 5px;
                        width: 78px;
                        height: 34px;
                        float: left;
                    }
                }
            }

            // 第三部分：上传作品结果
            .upload-result {
                margin: 0 auto;
                text-align: center;
                .uploading {
                    width: 100px;
                    height: 200px;
                    padding-top: 60px;
                    margin: 0 auto;
                    .upload-img {
                        width: 44px;
                        height: 44px;
                        margin: 0 auto;
                        border-radius: 50%;
                        background: url(/static/images_icon/sprite.png) center center no-repeat #fe6448;
                        background-size: auto 400px;
                    }
                    .error-img {
                        background-position: -165px -193px;
                    }
                    .success-img {
                        background-position: -165px -240px;
                    }
                    .upload-text {
                        font: {
                            size: smaller;
                            weight: bold;
                        }
                        line-height: 3;
                        color: #6d6969;
                    }
                }
                .help-message {
                    a {
                        display: inline;
                        text-decoration: underline;
                    }
                }
                .btn {
                    width: 78px;
                    height: 34px;
                    margin: 50px auto;
                }
            }
        }
    }
</style>


