<template>
    <div class="upgrade">
        <h1 class="title">升级为企业号</h1>
        <ul class="upgrade-text">
            <li>
                <h3>1、什么是企业号？</h3>
                <p>企业号是指您所下载的付费作品将有可能用于企业级的商业用途：原点网络的个人号下载的付费作品仅供个人使用，不得用于企业生产的任何产品当中。而企业所下载的付费作品可以用于该账号下的个人和企业两种身份。</p>
                <p>原点网络致力于保护素材作品作者的知识产权，实行账号的授权“一对一”保护措施，每一个付费作品的下载信息都将记录与作者的个人账户中，若发现任何侵权行为，原点网络将积极配合作品原作者追究其法律责任。</p>
            </li>
            <li>
                <h3>2、升级费用</h3>
                <p>个人号升级为企业号，需每年向平台缴纳<strong>300</strong>元的企业服务费。</p>
            </li>
        </ul>
        <o-button :text="'升级为企业号'" :classStyle="'main-btn'" class="upgrade-btn" @btnClick="goToUpgrade" v-show="!updateFlag"></o-button>
        <o-button :text="'您已是企业号'" :classStyle="'gray-btn'" class="over-btn" v-show="updateFlag"></o-button>
        <p class="last-date" v-show="updateFlag">您的企业号已经开通！到期时间为：2019-6-20</p>

        <!-- 艺术家认证弹窗 -->
        <upgrade-popup v-if="upgradeFlag" @goToUpgrade="goToUpgrade"></upgrade-popup>

    </div>
</template>
<script>

    import OButton from '../component/button.vue';
    import UpgradePopup from '../Popup/Upgrade/Upgrade.vue'
    export default {
        data() {
            return {
                upgradeFlag: false, // 艺术家认证弹窗显示的标识
                updateFlag: false, // 升级为企业号标识

            }
        },
        methods: {
            goToUpgrade (value) {
                this.upgradeFlag = !this.upgradeFlag;
                this.updateFlag = value;

            }
        },
        components: {
            OButton,
            UpgradePopup

        }
    }
</script>
<style lang="scss" rel="stylesheet/scss" scoped media="screen">
    .upgrade {
        .title {
            font-size: x-large;
            padding-bottom: 20px;
            text-align: center;
        }
        .upgrade-text {
            li {
                margin-bottom: 10px;
                h3 {
                    padding: 10px 0;
                }
            }
        }
        .upgrade-btn, .over-btn {
            width: 160px;
            height: 40px;
            margin: 100px auto 10px;
        }
        .over-btn {
            width: 200px;
        }
        .last-date {
            color: #969595;
            font-size: small;
            text-align: center;
        }
    }
</style>


