<template>
    <div class="pay-tab">
        <div class="header">
            <o-button :text="item.text" :classStyle="'gray-btn'" class="pay-btn" :class="{ active: curId === index }" @btnClick="changePayMethods(index)" v-for="(item, index) in payTabs" :key="index"></o-button>
        </div>
        <div class="pay-content" v-show="curId === index" v-for="(item, index) in payContents" :key="index">
            <img :src="item.src" :alt="item.text" class="pay-img" />
            <p class="text">{{ item.text }}</p>
        </div>
        <slot name="pay-message" class="text"></slot>
    </div>
</template>
<script>
    import OButton from '../component/button.vue';
    export default {
        data () {
            return {

                payTabs: [ // 支付tabs
                    { "text": "微信支付" },
                    { "text": "支付宝支付" }
                ],
                payContents: [ // 支付方式
                    { "src": "/static/images_icon/QR.png", "text": "打开微信扫一扫" },
                    { "src": "/static/images_icon/alipay.jpg", "text": "打开支付宝扫一扫" }
                ],
                curId: 0, // 切换支付方式的id
            }
        },
        methods: {

            // 改变支付方式
            changePayMethods (index) {
                this.curId = index;

            }
        },
        components: {
            OButton
        }
    }
</script>
<style lang="scss" rel="stylesheet/scss" scoped media="screen">
    .pay-tab {
        .header {
            margin: 24px auto;
            height: auto;
            text-align: center;
            .pay-btn {
                width: 120px;
                height: 38px;
                display: inline-block;
                &:first-child {
                    border-radius: 3px 0 0 3px;
                }
                &:last-child {
                    border-radius: 0 3px 3px 0;
                }
            }
            .active {
                color: #e5f5ff;
                background: #48ABFF;
                border-color: #48abff;
            }

        }
        .pay-content {
            margin: 10px auto;
            text-align: center;
            .pay-img {
                display: block;
                width: 200px;
                margin: 0 auto;
                height: 200px;
            }
        }
        p.text {
            line-height: 1;
            padding-top: 10px;
            text-align: center;
        }
    }
</style>


